import ReduxThunk from 'redux-thunk';
import { Ignite } from '@mornya/react-helper';
import reducers from '@/reducers';
var option = {
    application: {
        component: function () { return import('./App'); },
        rootElementId: 'app',
        onHotReload: function (next) { return module.hot && module.hot.accept('./App', next); },
    },
    store: {
        middlewares: [ReduxThunk],
        reducers: reducers,
        initialState: {},
    },
};
Ignite.init(option);
